@import url("https://fonts.googleapis.com/css2?family=Albert+Sans:wght@400;600;800&family=Inter&family=Source+Sans+Pro:wght@400;600&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  transition: all, cubic-bezier(0.075, 0.82, 0.165, 1) 1000ms;
}
form {
  height: 100%;
  display: flex;
  align-items: center;
  width: 100%;
}
